<template>
    <a-space wrap>
        <a-button type="primary" @click="getResult">Test</a-button>
    </a-space>
</template>

<script lang="ts" setup>
const getResult = async () => {
    console.log("hi")
    const url = 'http://106.15.231.37:5000/compute';
    // const url = 'http://localhost:5000/compute';
    const data = { a: "1", b: "2" };

    try {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        });

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const result = await response.json();
        console.log(result);
    } catch (error) {
        console.error('There was an error!', error);
    }
}
</script>